	module.exports = {"data":{"markdownRemark":{"html":"<p>Lisons <strong>1 Corinthiens 14:2‭-‬3‭, ‬27-28 :</strong></p>\n<blockquote>\n<p>« En effet, celui qui parle en langue <strong>ne parle pas aux hommes, mais à Dieu</strong>, car personne ne le comprend, et c’est en esprit qu’il dit des mystères. Celui qui prophétise, au contraire, parle aux hommes, les édifie, les exhorte, les console. En est-il qui parlent en langue, que deux ou trois au plus parlent, chacun à son tour, et que quelqu'un interprète; s’il n’y a point d’interprète, qu’on se taise dans l’Église, <strong>et qu’on parle à soi-même et à Dieu.</strong> »</p>\n</blockquote>\n<p>Selon l'apôtre Paul, il est inutile pour l'église que quelqu'un se mette à parler une langue inconnue car cela n'édifie personne. Il n'est pas dit, comme certains le pensent, que celui qui parle en langues ne s'adresse pas aux hommes, mais s'adresse uniquement à Dieu. En prenant soin de comprendre le verset 2 avec le verset 28, la Parole de Dieu déclare ici que si personne n'est en mesure d'interpréter, cette personne devrait <strong>simplement se taire dans l'Église!</strong> </p>\n<p>En d'autres termes tout ce qui n'édifie pas l'église est inutile et n'y a pas sa place ! Paul se prendra même en exemple pour illustrer ce point. </p>\n<p>Lisons <strong>1 Corinthiens 14:6‭-‬9, ‬18‭-‬19 :</strong></p>\n<blockquote>\n<p>« Et maintenant, frères et soeurs, en quoi vous serais-je utile si je venais chez vous en parlant en langues au lieu de vous apporter une parole de révélation, de connaissance, de prophétie ou d'enseignement? Si les objets inanimés qui rendent un son, comme une flûte ou une harpe, ne rendent pas des sons distincts, comment reconnaîtra-t-on la mélodie jouée sur eux? Et si la trompette rend un son confus, qui se préparera au combat? Il en va de même pour vous: si votre langue ne donne pas une parole intelligible, comment saura-t-on ce que vous dites? En effet, vous parlerez en l'air. Je remercie [mon] Dieu de ce que je parle en langues plus que vous tous. <strong>Mais, dans l'Eglise, j'aime mieux dire 5 paroles avec mon intelligence afin d'instruire aussi les autres, plutôt que 10'000 paroles en langue.</strong> »</p>\n</blockquote>\n<p>En conclusion, deux principes très clairs sont énoncés par Paul au sujet du parler en langue :</p>\n<ol>\n<li>Celui qui s'exprime dans l'église doit le faire de manière à être compris de tous. Dans le cas contraire, qu'il se taise... </li>\n<li>Tous ne doivent pas parler en même temps. Au maximum 3, avec un interprète, chacun à leur tour. Dans le cas contraire, qu'ils se taisent...</li>\n</ol>","frontmatter":{"title":"Comment Paul considère-t-il le fait de parler une langue inconnue et incompréhensible dans l'église ?"}}},"pathContext":{"slug":"/comment-paul-considere-t-il-lefait-de-parler-une-langue-inconnue-et-incompréhensible-dans-l-eglise/"}}

